import React from "react";

export default function Footer() {
  return (
    <>
      <div
        className="footer-main"
        // style={{
        //   padding: "80px 50px 50px 50px",
        //   background: "black",
        //   display: "flex",
        //   gap: "50px",
        //   flexWrap: "wrap",
        // }}
      >
        {/* About Section */}
        <div style={{ flex: "1 1 300px", minWidth: "250px" }}>
          <div style={{ width: "120px", height: "80px", position: "relative" }}>
            <img
              src="/assets/Logo.png"
              alt="Logo"
              style={{
                width: "100%",
                height: "100%",
                objectFit: "contain",
              }}
            />
          </div>
          <br />
          <span
            style={{
              color: "white",
              fontSize: "14px",
              letterSpacing: "1px",
            }}
          >
            Cherry Pickers Equipment is a platform and your perfect partner to
            trade used access platforms and other construction equipment.
          </span>
        </div>

        {/* Links Sections */}
        <div
          style={{
            flex: "3 1 600px",
            display: "flex",
            justifyContent: "space-between",
            flexWrap: "wrap",
            gap: "20px",
          }}
        >
          {/* Quick Links */}
          <div style={{ flex: "1 1 150px" }}>
            <span
              style={{
                color: "rgba(213, 162, 53, 1)",
                fontWeight: "bold",
                fontSize: "14px",
                letterSpacing: ".5px",
                marginBottom: "10px",
                display: "block",
              }}
            >
              Quick Links
            </span>
            <div
              style={{ display: "flex", flexDirection: "column", gap: "8px" }}
            >
              {[
                "Home",
                "About us",
                "Products",
                "Spare Parts",
                "Contact Us",
              ].map((link, i) => (
                <span key={i} style={{ color: "white", fontSize: "12px" }}>
                  {link}
                </span>
              ))}
            </div>
          </div>

          {/* Support Links */}
          <div style={{ flex: "1 1 150px" }}>
            <span
              style={{
                color: "rgba(213, 162, 53, 1)",
                fontWeight: "bold",
                fontSize: "14px",
                letterSpacing: ".5px",
                marginBottom: "10px",
                display: "block",
              }}
            >
              Support Links
            </span>
            <div
              style={{ display: "flex", flexDirection: "column", gap: "8px" }}
            >
              {[
                "test1@gmail.com",
                "test2@gmail.com",
                "+971 5655746478",
                "+971 6574648908",
                "Whatsapp Chat",
              ].map((item, i) => (
                <span key={i} style={{ color: "white", fontSize: "12px" }}>
                  {item}
                </span>
              ))}
            </div>
          </div>

          {/* Head Office */}
          <div style={{ flex: "1 1 200px" }}>
            <span
              style={{
                color: "rgba(213, 162, 53, 1)",
                fontWeight: "bold",
                fontSize: "14px",
                letterSpacing: ".5px",
                marginBottom: "10px",
                display: "block",
              }}
            >
              Head Office
            </span>
            <span style={{ color: "white", fontSize: "12px" }}>
              Office no 2029, ABN Victor Business center, Burjuman Business
              Tower, Bur Dubai, Dubai, UAE
            </span>
          </div>
        </div>
      </div>

      {/* Bottom Footer */}
      <div
        style={{
          display: "flex",
          justifyContent: "center", // center copyright
          background: "black",
          padding: "20px 0",
          fontSize: "12px",
          color: "white",
          textAlign: "center",
        }}
      >
        <span>All Rights Reserved By Cherry Pickers Equipment</span>
      </div>

      {/* Responsive CSS */}
      <style jsx>{`
        .footer-main {
          padding: 80px 50px 50px 50px;
          background: black;
          display: flex;
          gap: 50px;
          flex-wrap: wrap;
        }

        @media (max-width: 768px) {
          .footer-main {
            padding: 30px 20px; /* smaller padding on mobile */
          }
        }
      `}</style>
    </>
  );
}
